import { parsePageContent, parseXML } from "@/utils/xml.js";

export const state = () => ({
  id: null,
  data: null,
  selected: null,
  selectedTag: null,
  highlighted: null,
  isError: false,
  isReady: false,
  isLoading: false,
  xmlModel: parseXML(xml),
  wasSaved: false,
  wasEdited: false,
  editLayoutHorizontal: true,
  iiifUrl:
    "https://files.transkribus.eu/iiif/2/XITIHIQXRRBAHPZDHDPHWHMP/info.json",
  xmlJson: parsePageContent(xml),
  // iiifUrl: "",
  // xmlJson: null,
  xml: xml,
});

export const mutations = {
  update(state, params) {
    for (const key in params)
      if (!deepEqual(state[key], params[key])) state[key] = params[key];
  },
};

export const actions = {
  update({ commit }, params) {
    commit("update", params);
  },
  reset({ dispatch }) {
    dispatch("update", {
      id: null,
      data: null,
      selected: null,
      selectedTag: null,
      highlighted: null,
      isError: false,
      isready: false,
      isLoading: false,
      xmlModel: parseXML(xml),
      wasSaved: false,
      wasEdited: false,
      editLayoutHorizontal: true,
      iiifUrl:
        "https://files.transkribus.eu/iiif/2/XITIHIQXRRBAHPZDHDPHWHMP/info.json",
      xmlJson: parsePageContent(xml),
      // iiifUrl: "",
      // xmlJson: null,
      xml: xml,
    });
  },
  select({ dispatch, state }, { origin, target }) {
    dispatch("update", { selected: { origin, target } });
  },
  highlight({ dispatch, state }, { origin, target }) {
    dispatch("update", { highlighted: { origin, target } });
  },
  setSelectedTag({ dispatch, state }, { selectedTag }) {
    dispatch("update", { selectedTag: selectedTag });
  },
  setIIIF({ dispatch, state }, { iiifUrl }) {
    dispatch("update", { iiifUrl: iiifUrl });
  },
  setXmlJson({ dispatch, state }, { xmlJson }) {
    dispatch("update", { xmlJson: xmlJson });
  },
  setXml({ dispatch, state }, { xml }) {
    let json = parseXML(xml);

    dispatch("update", { xmlModel: json });

    let pageJson = parsePageContent(xml);

    dispatch("update", { xmlJson: pageJson });
  },
  setXmlModel({ dispatch, state }, { xmlModel }) {
    dispatch("update", { xmlModel: xmlModel });
  },
  async fetch({ state, dispatch }, id) {
    // if (id) dispatch('update', { id })
    dispatch("update", { isLoading: true, isReady: false });
    await dispatch("fetchPageContent", id);
    dispatch("update", { isLoading: false, isReady: true });
  },
  async fetchPageContent({ state, dispatch }, id) {
    if (id) dispatch("update", { id });
    const data = await fetchPageContentXML(id);
    if (data === null) dispatch("update", { data: null, isError: true });
    else {
      dispatch("update", {
        data: Object.freeze(data),
        isError: false,
      });
    }
  },
  findElementById({ state }, id) {
    const node = state.xmlJson;
    const stack = [node];
    while (stack.length) {
      const node = stack.pop();
      if (node.elements) node.elements.forEach((node) => stack.push(node));
      if (node.attributes && node.attributes.id === id) return node;
    }
    return null;
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
